* {
    margin:0;
    padding:0;
    border:none;
    outline:none;
    user-select:none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}
body {
    background:#161617;
    min-width:100vw;
    min-height:100vh;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack:center;
        align-content:center;
    overflow-x:hidden;
}
.slider {
    position:absolute;
    top:0%;
    left:0;
    height:100%; 
    width:100%;
    background-color: #1C1C1C;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index:2;
}
.particles {
    position:absolute;
    z-index:-2;
    left:0;
    top:0;
    width:100vw;
    height:100vh;
    overflow:hidden;
}


