.wrapper {
    position:relative;
    top:225px;
    width:100%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.projects {
    position:relative;
    width:75%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;    
}
.frame {
    width:200px;
    height:200px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    border:3px solid #2E2922;
    margin:25px;
    background-size:cover;
    background-position:center;
    cursor:pointer;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
}
.frameName {
    position:relative;
    top:84%;
    width:100%;
    height:16%;
    background-color:black;
    opacity:0.5;
    color:white;
    font-size:22px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight:600;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
}
.frame:hover {
    -webkit-transform: scale(1.2) rotate(10deg);
        -ms-transform: scale(1.2) rotate(10deg);
            transform: scale(1.2) rotate(10deg);
}
.frame:hover .frameName {
    opacity:1;
    height:20%;
    top:80%;
    font-size:26px;
}
@media (min-width: 768px) and (max-width: 1280px) {
    .frameName {
        font-size:20px;
    }
    .frame:hover .frameName {
        font-size:24px;
    }
}
@media (min-width: 320px) and (max-width: 767px) {
    .frameName {
        font-size:16px;
    }
    .frame:hover .frameName {
        font-size:20px;
    }
}
@media (max-width:319px) {
    .frameName {
        font-size:12px;
    }
    .frame:hover .frameName {
        font-size:16px;
    }
}


