.startCircle {
    position:relative;
    top:37.5%;
    height:250px;
    width:250px;
    border-radius:50%;
    font-size:72px;
    font-family:sans-serif;
    font-weight:700;
    background-image:url('Images/dog.jpg');
    background-position:center;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.start {
    position:relative;
    top:25px;
    height:200px;
    width:200px;
    border-radius:50%;
    font-size:72px;
    font-family:sans-serif;
    font-weight:700;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
    z-index:2;
    cursor:pointer;
}
.start:hover {
    color:white;
    background-color:red;
    font-size:0px;
}


